
.header {
  border: 1px solid #f3f0f0;
  box-shadow: 0 3px 5px #e6e6e6;
  line-height: 56px;
  margin-right: -15px;
  margin-left: -15px;
  padding-left: 10px;
  font-size:18px;
  margin-bottom: 50px;
}
.header a{
  display: inline-block;
  padding-left: 10px;
}
img{
  width:36px;
  height: 36px;
  vertical-align: middle;
  margin-right: 10px;
}



